 demographic information like area pin code, employment status (working/studying), phone number. We use this data to provide you with right content based on your interest/preference.")]),_c('p',{staticClass:"list-head"},[_vm._v("Cookies")]),_c('p',{staticClass:"content"},[_vm._v("A cookie is a small piece of information stored by a web server on a web browser so it can be later read back from that browser. Cookies are useful for enabling the browser to remember information specific to a given user. We place both permanent and temporary cookies in your memory storage device. Cookies can be cleared from browsers clear cache option.")]),_c('p',{staticClass:"list-head"},[_vm._v("3. Security Precautions")]),_c('p',{staticClass:"content"},[_vm._v("Our Website has stringent security measures in place to protect the loss, misuse, and alteration of the information under our control. Whenever you change or access your account information, we offer the use of a secure server. Once your information is in our possession we adhere to strict security guidelines, protecting it against unauthorized access.")]),_c('p',{staticClass:"list-head"},[_vm._v("4. Links to Other Sites/Apps")]),_c('p',{staticClass:"content"},[_vm._v("Our Website links to other websites/Websites that may collect personally identifiable information about you. Our Website (ezenotes.com) is not responsible for the privacy practices or the content of those linked websites/Websites.")]),_c('p',{staticClass:"list-head"},[_vm._v("5. Sharing of personal information")]),_c('p',{staticClass:"content"},[_vm._v("We may share personal information with our other corporate entities and affiliates. These entities and affiliates may market to you as a result of such sharing unless you are explicitly opt-out.")]),_c('p',{staticClass:"content"},[_vm._v("We may disclose personal information to third parties. This disclosure may be required for us to provide you access to our Services, to comply with our legal obligations, to enforce our User Agreement, to facilitate our marketing and advertising activities, or to prevent, detect, mitigate, and investigate fraudulent or illegal activities related to our Services. We do not disclose your personal information to third parties for their marketing and advertising purposes without your explicit consent.")]),_c('p',{staticClass:"content"},[_vm._v("We may disclose personal information if required to do so by law or in the good faith belief that such disclosure is reasonably necessary to respond to subpoenas, court orders, or other legal processes. We may disclose personal information to law enforcement offices, third party rights owners, or others in the good faith belief that such disclosure is reasonably necessary to: enforce our Terms or Privacy Policy; respond to claims that an advertisement, posting or other content violates the rights of a third party; or protect the rights, property or personal safety of our users or the general public.")]),_c('p',{staticClass:"content"},[_vm._v("We and our affiliates will share/sell some or all of your personal information with another business entity should we (or our assets) plan to merge with, or be acquired by that business entity, or reorganization, amalgamation, restructuring of the business. Please contact us for any queries/concerns.")]),_c('p',{staticClass:"list-head"},[_vm._v("6. Opt-In/Opt-Out")]),_c('p',{staticClass:"content"},[_vm._v("We provide all users with the opportunity to opt-out of receiving non-essential (promotional, marketing-related) communications from us on behalf of our partners, and from us in general, after setting up an account. If you want to remove your contact information from all our lists and newsletters, write to unsubscribe-ezenotes@diatoz.com.")]),_c('p',{staticClass:"list-head"},[_vm._v("7. Advertisements on the Website")]),_c('p',{staticClass:"content"},[_vm._v("We use third-party advertising companies to serve ads when you visit our Website. These companies may use i