<template>
  <div id="eze-superform" :class="page != 'print' ? 'p-3' : ''">
    <div class="mt-2" v-for="(field, key)  in formData" :key="key">
      <label v-if="formData[key].label" class="label-field mb-0">{{formData[key].label}}</label>
      <br v-if="formData[key].label" />
      <p
        v-if="formData[key].validation.test"
        class="form-valid errorvib"
      >{{formData[key].validation.msg}}</p>
      <span
        v-if="formData[key].dropdown || formData[key].count || formData[key].radioAddress || formData[key].radioTerms"
        :id="append('*',formData[key].label,key)"
      >
        <b-form-select
          :id="append('_',formData[key].label,key)"
          v-if="formData[key].dropdown"
          :placeholder="formData[key].placeholder"
          class="input-field mb-2"
          :class="{invalid:formData[key].validation.test}"
          @input=" preValidation(formData[key],key)"
          v-model="formData[key].value"
          :options="formData[key].options"
          required
          :disabled="formData[key].disabled"
        ></b-form-select>
        <!-- start of counter -->
        <div class="container-count" v-if="formData[key].count">
          <div class="count-img-div" @click="addCopy(key)">
            <img class="count-img" src="@/assets/add.svg" />
          </div>
          <div class="count-num">{{formData[key].value}} copy</div>
          <div class="count-img-div" @click="reduceCopy(key)">
            <img class="count-img" src="@/assets/subtract.svg" />
          </div>
        </div>
        <!-- end of counter -->
        <!-- merchant radio buttons -->
        <div
          class="merchant-details"
          v-if="formData[key].radioAddress"
          @input=" preValidation(formData[key],key)"
        >
          <b-form-group v-if="formData[key].options.length">
            <span v-for="(item,index) in formData[key].options" :key="index">
              <b-form-radio
                v-model="formData[key].value"
                name="some-radios"
                :value="item.user_id"
                :id="getId(index,item.user_id)"
              >
                <p class="shop-name">{{item.name}}</p>
                <p class="shop-address">{{item.address}}</p>
                <p class="contact">
                  <span class="contact-label">Contact:</span>
                  {{item.phone_number}}
                </p>
              </b-form-radio>
              <hr />
            </span>
          </b-form-group>
          <p v-else>
            <span class="main-msg">Sorry you cannot complete this step.</span>
            <br />No xerox shops linked to your college as we will show only linked xerox shop here.
          </p>
        </div>
        <!-- end of merchant buttons -->
        <!-- start of accept terms and conditions -->

        <div v-if="formData[key].radioTerms" class="text-left">
          <w-policy-madal />
          <input
            type="checkbox"
            v-model="formData[key].value"
            @input=" preValidation(formData[key],key)"
          />
          <label class="terms ml-2">
            I agree with
            <span
              class="terms-button"
              @click="$bvModal.show('privacy-policy-modal')"
            >Terms and Conditions</span>
          </label>
        </div>
        <!-- end of accept terms and conditions -->
      </span>
      <input
        v-else
        class="input-field mb-2"
        :class="formData[key].disabled ? 'input-field-disabled' : {invalid:formData[key].validation.test}"
        :id="append('*',formData[key].label,key)"
        v-model="formData[key].value"
        @input=" preValidation(formData[key],key)"
        :placeholder="formData[key].placeholder"
        :type="field.type"
        required
        :disabled="formData[key].disabled"
        :autofocus="formData[key].autofocus"
        @keypress="isNumber($event,field.type)"
      />
    </div>
    <span v-if="!hideBtn">
      <b-row class="dual-btn" v-if="cancelBtn ">
        <div class="pusher"></div>
        <b-button
          variant="outline-danger"
          id="first_btn"
          class="first-btn"
          @click="sendCancel()"
        >Cancel</b-button>
        <b-button
          variant="danger"
          id="second_btn"
          class="second-btn"
          @click="sendUserData()"
        >{{btnText}}</b-button>
      </b-row>
      <b-button
        v-else
        :style="page == 'print' ? 'display:none' : ''"
        type="submit"
        variant="danger"
        id="main_btn"
        @click="sendUserData()"
        class="login-btn"
      >{{btnText}}</b-button>
    </span>
  </div>
</template>
<script>
import PolicyModal from "../widgets/privacyPolicyModal";
export default {
  props: [
    "formData",
    "btnText",
    "disabled",
    "page",
    "cancelBtn",
    "hideBtn",
    "trigger"
  ],
  components: {
    "w-policy-madal": PolicyModal
  },
  data() {
    return {
      allvalid: true
    };
  },
  watch: {
    trigger: function() {
      this.sendUserData();
    }
  },
  computed: {},
  methods: {
    isNumber(evt, type) {
      if (type == "number") {
        evt = evt ? evt : window.event;
        var charCode = evt.which ? evt.which : evt.keyCode;
        if (
          charCode > 31 &&
          (charCode < 48 || charCode > 57) &&
          charCode !== 46 || charCode === 46
        ) {
          evt.preventDefault();
        } else {
          return true;
        }
      } return true;
    },
    getId(var1, var2) {
      //console.log(var1 +"+" +var2)
      let rvalue = var1 + "+" + var2;
      rvalue = rvalue.toString();
      return rvalue;
    },
    append(separator, item1, item2) {
      let result = item1 + separator + item2;
      return result.replace(/ /g, "_");
    },
    sendCancel() {
      this.$emit("cancel");
    },
    preValidation(data, number) {
      // console.log(data, number);
      this.$emit("changing", data);
      var type = data.validation.type;
      if (type == "textname") {
        this.pretextname(number);
      } else if (type == "textnumber") {
        this.pretextnumber(number, data);
      } else if (type == "textemail") {
        this.pretextemail(number);
      } else if (type == "mcq") {
        this.premcq(number);
      } else if (type == "radio") {
        this.preradio(number);
      }
    },
    pretextname(num) {
      this.formData[num].validation.test = false;
    },
    pretextemail(num) {
      this.formData[num].validation.test = false;
    },
    pretextnumber(num, data) {
      var max = this.formData[num].validation.max;
      // var min = this.formData[num].validation.min;
      var actual = this.formData[num].value.length;
      this.formData[num].validation.test = false;
      if (data.returnType == "percent_covered") {
        if (data.value > 100) {
          this.formData[num].validation.test = true;
          if (actual > max) {
            this.formData[num].validation.test = true;
          }
        } else {
          this.formData[num].validation.test = false;
          if (actual > max) {
            this.formData[num].validation.test = true;
          }
        }
      } else {
        if (actual > max) {
          this.formData[num].validation.test = true;
        }
        // if (actual < min) {
        //   this.formData[num].validation.test = true;
        // }
      }
      // this.formData[num].validation.test = false;
    },
    premcq(num) {
      this.formData[num].validation.test = false;
    },
    preradio(num) {
      this.formData[num].validation.test = false;
    },
    sendUserData() {
      this.submitValidation();
      this.emitData();
    },
    emitData() {
      // alert(this.allvalid)
      if (this.allvalid) {
        var result = "";
        result += "{";
        for (var a = 0; a < this.formData.length; a++) {
          if (this.formData[a].returnType) {
            result +=
              '"' +
              this.formData[a].returnType +
              '":"' +
              this.formData[a].value +
              '",';
          }
        }
        result = result.slice(0, result.length - 1);
        result += "}";
        result = JSON.parse(result);
        this.$emit("Submit", result);
        // console.log("emiting data",result)
      }
    },
    submitValidation() {
      // console.log("all data",this.formData)
      this.allvalid = true;
      for (var a = 0; a < this.formData.length; a++) {
        var type = this.formData[a].validation.type;
        if (type == "textname") {
          this.textname(a);
        } else if (type == "textnumber") {
          this.textnumber(a);
        } else if (type == "textemail") {
          this.textemail(a);
        } else if (type == "mcq") {
          this.mcq(a);
        } else if (type == "count") {
          this.counts(a);
        } else if (type == "radio") {
          this.textradio(a);
        }
      }
    },
    textradio(num) {
      if (!this.formData[num].value) {
        this.formData[num].validation.test = true;
        this.allvalid = false;
      }
    },
    textname(num) {
      //console.log("textname", num);
      if (!this.formData[num].value.length) {
        this.formData[num].validation.test = true;
        this.allvalid = false;
      }
     // to allow some empty fields
       if (this.formData[num].allowEmpty && !this.formData[num].value.length) {
        this.formData[num].validation.test = false;
        this.allvalid = true;
      }
    },
    textemail(num) {
      this.formData[num].validation.test = this.emailstrict(
        this.formData[num].value
      );
      this.allvalid = !this.emailstrict(this.formData[num].value);
      // to allow some empty fields
      //this.formData[num].value not becoming string datatype in cloud(server) so this condition(only for the cloud purpose)
      //so in cloud that was throwing an error length of undefined when no value entered.
       if(this.formData[num].value == "" && this.formData[num].allowEmpty){
        this.formData[num].validation.test = false;
        this.allvalid = true;  
       }
       // end of cloudonly condition
       if (this.formData[num].allowEmpty && !this.formData[num].value ) {
        this.formData[num].validation.test = false;
        this.allvalid = true;
      }
    },
    emailstrict(inputText) {
      if (inputText === undefined || inputText === null) {
        return true;
      } else {
        //eslint-disable-next-line
        var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (inputText.match(mailformat)) {
          return false;
        } else {
          return true; //invalid mail
        }
      }
      //console.log("email validataions",inputText)
    },
    counts(num) {
      //console.log("count value pa", num, this.formData[num].value);
      if (!this.formData[num].value) {
        this.formData[num].validation.test = true;
        this.allvalid = false;
      }
    },
    textnumber(num) {
      if (
        this.formData[num].value.length < this.formData[num].validation.min ||
        this.formData[num].value.length > this.formData[num].validation.max
      ) {
        this.formData[num].validation.test = true;
        this.allvalid = false;
      }
    },
    mcq(num) {
      let blackList = [
        "--------SEMESTER 1-2--------",
        "--------SEMESTER 3--------",
        "--------SEMESTER 4--------",
        "--------SEMESTER 5--------",
        "--------SEMESTER 6--------",
        "--------SEMESTER 7--------",
        "--------SEMESTER 8--------",
        "--------BANGALORE--------",
        "--------BELAGAVI--------",
        "--------KALABURGI--------",
        "--------MYSURU--------",
        "Select your college",
        "Select your branch",
        "Select your semester",
        "Select subject name",
        "Select module number"
      ];
      if (
        !this.formData[num].value.length ||
        blackList.indexOf(this.formData[num].value) > -1
      ) {
        this.formData[num].validation.test = true;
        this.allvalid = false;
      }
    },
    //for count input type
    addCopy(num) {
      this.formData[num].value++;
      this.formData[num].validation.test = false;
    },
    reduceCopy(num) {
      if (this.formData[num].value > 0) {
        this.formData[num].value--;
      }
    }
  }
};
</script>
<style scoped>
.main-msg {
  color: #fa3b4e;
  font-size: 1.5rem;
  font-weight: 900;
  text-align: center;
  font-style: italic;
  /* margin-bottom: 2rem; */
  display: block;
}
.pusher {
  width: 20%;
}
.first-btn {
  width: 35%;
  margin-top: 1rem;
  color: red;
  font-size: 19px;
  font-weight: 500;
  height: 48px;
  margin-right: 1rem;
}
.second-btn {
  margin-top: 1rem;
  width: 35%;
  color: white;
  font-size: 19px;
  font-weight: 500;
  height: 48px;
}
.merchant-details {
  border: 1px solid #d0d0d0;
  border-radius: 2px;
  opacity: 1;
  padding: 20px;
}
.shop-name {
  letter-spacing: 0px;
  color: #222c2c;
  opacity: 1;
  font-size: 16px;
  margin-bottom: 10px;
}
.shop-address {
  letter-spacing: 0px;
  color: #222c2c;
  opacity: 1;
  font-size: 14px;
  margin-bottom: 10px;
}
.contact-label {
  letter-spacing: 0px;
  color: #92949e;
  opacity: 1;
  font-size: 14px;
  margin-bottom: 10px;
}
.terms {
  letter-spacing: 0px;
  color: #92949e;
  opacity: 1;
  font-size: 14px;
  margin-bottom: 0px;
}
.terms-button {
  color: #686464;
  cursor: pointer;
}
.terms-button:hover {
  text-decoration: underline;
}
.contact {
  margin-bottom: 10px;
  font-size: 14px;
}
.container-count {
  display: flex;
  margin: auto;
}
.count-img-div {
  background: #f05c61 0% 0% no-repeat padding-box;
  opacity: 1;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 30px;
}
.count-img {
  margin: auto;
  display: block;
  margin-top: 0.6rem;
  cursor: pointer;
}
.count-num {
  /* margin-top: 0.6rem; */
  letter-spacing: 0px;
  color: #989898;
  opacity: 1;
  font-size: 30px;
}
.form-valid {
  background: white;
  width: fit-content;
  position: relative;
  top: 5px;
  border-radius: 4px;
  padding: 5px 10px 5px 10px;
  text-align: left;
  margin-left: 1rem;
  font-size: 13px;
  margin-bottom: 0px;
  color: #e34242;
  font-weight: 600;
  letter-spacing: 0.1rem;
  border: 1px solid red;
  font-style: italic;
}
.err-msg {
  font-size: 10px;
  color: pink;
  float: right;
}
.label-field {
  font-family: Helvetica;
  float: left;
  font-size: 14px;
  color: black;
  font-weight: 500;
  width: 80%;
  text-align: left;
}
.login-btn {
  margin-top: 1rem;
  width: 100%;
  color: white;
  font-size: 19px;
  font-weight: 500;
  height: 48px;
}
.input-field {
  width: 100%;
  height: 45px;
  font-size: 14px;
  border-radius: 2px;
  border: none;
  padding-left: 10px;
  background-color: white;
  color: #686565;
  border: 1px solid #d0d0d0;
}
.input-field::placeholder {
  font-size: 14px;
  /* background-color: white; */
  color: #bcbcbc;
}
.input-field-disabled {
  width: 100%;
  height: 45px;
  font-size: 14px;
  border-radius: 2px;
  border: none;
  padding-left: 10px;
  background-color: #e9ecef;
  color: #686565;
  border: 1px solid #d0d0d0;
}
.alert-text {
  font-size: 12px;
  font-family: Helvetica;
  color: #e3606f;
  opacity: 100%;
  text-align: center;
}
.invalid {
  border: 1px solid red;
}
.errorvib {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
  transition: all 1s;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
/deep/.custom-select:disabled {
    color: #6c757d !important;
    background-color: #e9ecef  !important;
}
</style>
<style>
#eze-superform input[type="radio"],
input[type="checkbox"] {
  vertical-align: middle;
}
</style>