<template>
  <div class="clients">
    <b-row class="top-row">
      <b-col cols="9">
        <h2 class="top-heading">
          <i class="fa fa-window-restore mr-3" aria-hidden="true"></i>Clients
        </h2>
      </b-col>
      <b-col>
        <b-button class="add_button" v-b-modal.modal-add-client> ADD CLIENT</b-button>
      </b-col>
    </b-row>

    <b-container class="mt-3" fluid style="white-space:pre-wrap;">
      <b-table ref="table" show-empty hover bordered stacked="md" :items="items" :fields="fields">
        <template v-slot:cell(serial)="row">{{row.index + 1}}</template>
        <template v-slot:cell(preview)="row">
          <i class="fa fa-eye" @click="preview(row.index)" aria-hidden="true"></i>
        </template>
        <template v-slot:cell(last_sync)>{{ new Date() | moment(" MMMM Do YYYY, h:mm:ss a") }}</template>
        <template v-slot:cell(status)="row">
          <div class="status-btn" :class="status(row.index)">{{row.item.status}}</div>
        </template>
        <template v-slot:cell(onoff)="row">
          <b-form-checkbox @input="onoff(row.index)" v-model="checked" name="check-button" switch></b-form-checkbox>
        </template>
        <template v-slot:cell(edit)="row">
          <div @click="edit(row.index)" class="push-btn">
            <i class="fa fa-rocket" aria-hidden="true"></i>
          </div>
        </template>
        <template v-slot:cell(remove)="row">
          <i class="fa fa-trash" @click="remove(row.index)" aria-hidden="true"></i>
        </template>
      </b-table>
    </b-container>
    <b-modal id="modal-add-client" hide-footer title="ADD CLIENT">
      <w-super-form
        :form-data="type"
        @Submit="submitFiles($event)"
        :btnText="'ADD'"
        :cancelBtn="true"
        @cancel="cancelGoBack()"
      />
    </b-modal>

    <b-modal id="modal-preview" hide-footer hide-header>
      <i class="fa fa-times" aria-hidden="true" @click="closePreview()"></i>
      <preview :type="clickedpreview"/>
    </b-modal>
  </div>
</template>
<script>
import superform from "@/components/widgets/superForm";
import preview from "@/components/widgets/preview-modal";
export default {
  components: {
    "w-super-form": superform,
    preview
  },
  data() {
    return {
      clickedpreview:"",
      type: [
        {
          type: "text",
          label: "Device Name",
          placeholder: "Device Name",
          value: "",
          validation: {
            type: "textname",
            test: false,
            msg: "Device Name can't be empty"
          },
          returnType: "device_name", //device_name
          disabled: false
        },
        {
          type: "text",
          label: "Device Type *",
          placeholder: "Device Type",
          validation: {
            type: "mcq",
            test: false,
            msg: "Device type can't be empty"
          },
          value: "",
          dropdown: true,
          options: [
            "MONITOR",
            "DOT MATRIX",
            "THERMAL PRINTER",
            "ROLLER BANNER"
          ],
          returnType: "device_type", //printType
          disabled: false
        }
      ],
      items: [
        {
          type: "hdmi",
          device_name: "Status Shop",
          device_id: "Ku2RdP",
          device_type: "DOT MATRIX",
          status: "ON"
          // last_sync:"12/12/15 11pm"
        },
        {
          type: "hdmi",
          device_name: "Entrance01",
          device_id: "kdKgwR",
          device_type: "MONITOR",
          status: "ON"
          // last_sync:"12/12/15 11pm"
        },
        {
          type: "hdmi",
          device_name: "Banaswadi shop",
          device_id: "JFdpwn",
          device_type: "ROLLER BANNER",
          status: "ON"
          // last_sync:"12/12/15 11pm"
        },
        {
          type: "hdmi",
          device_name: "Shop bill machine",
          device_id: "LaR5v8",
          device_type: "THERMAL PRINTER",
          status: "OFF"
          // last_sync:"12/12/15 11pm"
        }
      ],
      fields: [
        { key: "serial", label: "SlNo", class: "text-center" },
        { key: "device_id", label: "Device id" },
        { key: "edit", label: "Edit", class: "text-center" },
        { key: "device_name", label: "Device name" },
        { key: "status", label: "Status", class: "text-center" },
        { key: "last_sync", label: "Last sync", class: "text-center" },
        { key: "preview", label: "Preview", class: "text-center" },
        { key: "device_type", label: "Type" },
        { key: "onoff", label: "switch", class: "text-center" },
        { key: "remove", label: "Remove", class: "text-center" }
      ]
    };
  },
  computed: {},
  mounted() {},
  methods: {
    preview(index) {
      console.log("preview -", index);
      this.clickedpreview = this.items[index].device_type;
      this.$bvModal.show("modal-preview");
    },
    onoff(index) {
      console.log("onoff", index);
    },
    remove(index) {
      console.log("remove -", index);
    },
    status(index) {
      if (this.items[index].status == "ON") return "GREEN";
      else return "RED";
    },
    edit(index) {
      console.log("edit -", index);
      this.$router.push({
        path: "/edit"
      });
    },
    submitFiles(event) {
      console.log("received values", event);
      var randomstring = require("randomstring");
      event.device_id = randomstring.generate(6);
      event.status = Math.floor(Math.random() * 10) > 5 ? "ON" : "OFF";
      this.$bvModal.hide("modal-add-client");
      this.items.push(event);
      this.cleanFields();
    },
    cleanFields() {
      for (var a = 0; a < this.type.length; a++) {
        this.type[a].value = "";
      }
    },
    cancelGoBack() {
      this.$bvModal.hide("modal-add-client");
    },
    closePreview(){
      this.$bvModal.hide("modal-preview");
    }
  }
};
</script>
<style scoped>
.add_button,
.add_button:hover {
  color: #0c177d;
  background-color: transparent;
  border-color: #0c177d;
  margin: 0 auto;
  display: block;
  font-weight: 900;
  z-index: 0;
  position: relative;
}
.add_button:focus {
  box-shadow: none;
  color: #0c177d;
  background-color: transparent;
  border-color: #0c177d;
  margin: 0 auto;
  display: block;
  font-weight: 900;
  z-index: 0;
  position: relative;
}
.add_button:active {
  box-shadow: 0 0 0 0.2rem #d3d30880;
  color: white;
  background-color: #0c177d !important;
  border-color: #0c177d;
  margin: 0 auto;
  display: block;
  font-weight: 900;
  z-index: 0;
  position: relative;
}
.top-row {
  width: 90%;
}
.top-heading:after {
  content: "";
  position: absolute;
  width: 50%;
  display: block;
  height: 2px;
  background-color: #2c3e50;
  bottom: -5px;
}
.fa-eye,
.fa-edit,
.fa-trash {
  color: #0c177d;
  cursor: pointer;
}
.push-btn {
  font-weight: 700;
  height: 100%;
  width: 100%;
  background-color: #0c177d;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  font-size: 15px;
}
.status-btn {
  font-weight: 700;
  height: 100%;
  width: 100%;
  background-color: #0ceb04;
  border-radius: 4px;
  color: white;
  font-size: 15px;
}
.push-btn:active {
  font-weight: 700;
  height: 100%;
  width: 100%;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}
.GREEN {
  background-color: GREEN;
}
.RED {
  background-color: RED;
}
.fa-times{
  position:absolute;
  right:.3rem;
  top:0rem;
  font-size:2rem;
  cursor: pointer;
  color:grey;
}
.fa-times:hover{
  font-size:2.3rem;
  color:black;

}
/deep/.modal-dialog{
  max-width:fit-content;
}
</style>